@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@400;700&display=swap);
.homepage-conatiner__hero {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  text-align: center;
  margin-top: 8rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.hero-item {
  width: 40%;
  line-height: 1.2;
  font-size: 1.3rem;
}

.hero-item img {
  width: 100%;
  height: 450px;
  margin-bottom: 1rem;
  object-fit: cover;
}

.homepage-container__showcase h1 {
  margin-top: 3rem;
  text-align: center;
}

.homepage-container__showcase-slider {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 90%;
  max-width: 1440px;
  margin: 0 auto;
}

.slider-item {
  width: 30%;
  margin: 0.4rem 1rem;
}

.homepage-container__showcase-slider img {
  height: 300px;
  width: 350px;
  object-fit: contain;
}



@media (max-width: 768px) {
  .hero-item {
    width: 80%;
    margin: 1rem auto;
    font-size: 1rem;
  }

  .hero-item img {
    height: 400px;
  }

  .homepage-container__showcase-slider {
    overflow-x: scroll;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
  }

.homepage-container__showcase-slider img {
  width: 200px;
  height: 200px;
}

  .slider-item {
    width: 80%;
  }
}

.header-container {
    width: 100%;
    position: fixed;
    top: 0;
    background-color: transparent;
    transition: all 0.3s ease-in-out;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 0.5rem;
}

.header-container__motto p {
    margin: 0;
    padding: 0;
    font-size: 0.7rem;
}

.header-container__cart {
    cursor: pointer;
}

* {
  box-sizing: border-box;
  font-family: "Josefin Sans", Arial, Helvetica, sans-serif;
}

body,
html {
  padding: 0;
  margin: 0;
  background-color: #fcf8f3;
  color: #1a1212;
}

/* ============== custom classes */

.btn {
  padding: 1rem;
  letter-spacing: 2px;
  text-transform: uppercase;
  background-color: #241c15;
  color: #fff;
  border: none;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  outline: none;
}

.btn:hover {
  background-color: transparent;
  border: 1px solid #241c15;
  color: #241c15;
}

