.header-container {
    width: 100%;
    position: fixed;
    top: 0;
    background-color: transparent;
    transition: all 0.3s ease-in-out;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0.5rem;
}

.header-container__motto p {
    margin: 0;
    padding: 0;
    font-size: 0.7rem;
}

.header-container__cart {
    cursor: pointer;
}
